<!--<script src="../../../movie_service/config.js"></script>-->
<template>
  <div style="">
    <Card style="margin: 100px 40px 20px 40px;border-radius: 20px">
      <div>
        <movie-index-header></movie-index-header>
      </div>
      <h1 style="text-align: left;margin-left: 23px;font-style: italic"></h1>
      <List item-layout="vertical" style="text-align: left;margin-left: 10px;">
        <ListItem v-for="(item,index) in items" :key="item._id">
          <router-link :to="{path: '/articleDetail', query:{id:item._id}}">
            <ListItemMeta :avatar="item.userAvatar" :title="item.title" :description="item.description" />

            <span style="margin-left: 50px;color: black;">
              {{ToText(item.context)}}

            </span>

            <!--      {{item.content.length <= 40 ? item.content : item.content.substring(0, 40) + '...'}}}-->


            <template>
              <div>
              <img :src="item.img" style="width: 280px">
              </div>
            </template>
            <span style="padding-top: 10px;">
              <span v-on:click="support(item._id,index)" style="color: #88B091">
                <Icon type="ios-thumbs-up-outline"/>
                {{ item.num }}
              </span>
            </span>
            <Divider type="vertical" />
            <span v-if="isAdmin" >
              <span>
                <a style="text-decoration: underline;color: #88B091" v-on:click="delArticle(item._id)"></a>
              </span>
            </span>
            <Divider type="vertical" />
            <span style="text-align: right;color: grey">{{ item.time}}</span>

          </router-link>

        </ListItem>

      </List>
      <Page :current="index" :total="totalNum" :page-size="pageSize" show-elevator style="text-align: center;margin-top: 50px"
            @on-change="changePage"/>
      <div style="text-align: left;padding-left: 30px;padding-top: 30px">
        <Divider orientation="right"></Divider>
        <span style="font-weight: bold"></span>
        <input type="text" v-model="article.title" placeholder=".." clearable class="tt"></input>
        <div style="margin-top: 10px">
          
          <input type="text" v-model="article.description" placeholder="." clearable class="ttb"></input>
        </div>
        <p style="text-align: right;margin: 10px 40px 0px 0px ">
          {{ article.time }}
        </p>
      </div>

      <div style="height: 400px;padding-left: 30px;padding-right: 30px">
        <!-- -->
        <Upload
          class="avatar-uploader"
          action="https://up-z2.qiniup.com"
          name="file"
          :data="qiniuForm"
          :show-file-list="false"
          :on-success="uploadSuccess"
          :on-error="uploadError"
          :before-upload="beforeUpload"
        >
        </Upload>
        <!---->
        <el-row v-loading="uillUpdateImg">
          <quill-editor
            v-model="detailContent"
            ref="myQuillEditor"
            :options="editorOption"
            @change="onEditorChange($event)"
            @ready="onEditorReady($event)"
            style="height: 300px"
          >
          </quill-editor>
        </el-row>

      </div>
      <div>
        <Button style="margin:50px 20px 20px 20px;width: 100px" type="primary" v-on:click="save"></Button>
      </div>
    </Card>
  </div>
</template>

<script>
import MovieIndexHeader from "../components/MovieIndexHeader";

import ImageResize from 'quill-image-resize-module' //
Quill.register('modules/imageResize', ImageResize)

const toolbarOptions = [
  ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
  ['blockquote', 'code-block'],

  [{'header': 1}, {'header': 2}],               // custom button values
  [{'list': 'ordered'}, {'list': 'bullet'}],
  [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
  [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
  [{'direction': 'rtl'}],                         // text direction

  [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
  [{'header': [1, 2, 3, 4, 5, 6, false]}],

  [{'color': []}, {'background': []}],          // dropdown with defaults from theme
  [{'font': []}],
  [{'align': []}],
  ['link', 'image'],
  ['clean']                                         // remove formatting button
]

export default {
  name: "articlePage",
  components: {MovieIndexHeader},
  data() {
    return {
      article: {
        ss: '',
        title: '',
        username: '',
        time: new Date(),
        context: '',
        avatar: '',
        description: ''

      },
      items: [],
      isAdmin: false,
      totalNum: 1,
      index: 1,
      pageSize: 3,
      domain: 'https://up-z2.qiniup.com',
      // 
      qiniuaddr: 'qmaqpkabs.hn-bkt.clouddn.com',
      qiniuForm: {
        key: '',
        token: ''
      },
      uillUpdateImg: '',
      quillUpdateImg: false, // loadingfalse,
      serverUrl: 'https://up-z2.qiniup.com',  // 
      header: {token: sessionStorage.token},  // token
      detailContent: '', // 
      editorOption: {
        placeholder: '',
        theme: 'snow',  // or 'bubble'
        modules: {
          toolbar: {
            container: toolbarOptions,  // 
            handlers: {
              'image': function (value) {
                if (value) {
                  // alert("1");
                  document.querySelector('.avatar-uploader input').click()
                } else {
                  this.quill.format('image', false);
                }
              }
            }
          },
          imageResize: {   //
            displayStyles: {   //
              backgroundColor: 'black',
              border: 'none',
              color: 'white'
            },
            modules: ['Resize', 'DisplaySize', 'Toolbar']   //
          },
        }
      }
    }
  },
  methods: {
    // 
    async beforeUpload(file) {
      this.quillUpdateImg = true
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      // const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error(' JPGPNG !')
        return reject(false)
      }
      if (isJPG == false) return false;

      //token
      let fileType = file.type == 'image/jpeg' ? 'jpg' : 'png'; //
      let res = await this.axios.get('http://127.0.0.1:3000/upload/token')
      console.log(res);
      console.log(res.data.data.token)
      // return;
      this.qiniuForm = {
        key: 'movieImg_test_' + new Date().getTime() + Math.floor(Math.random() * 100) + '.' + fileType, //
        token: res.data.data.token  // 
      }
      this.$Notice.info({title: ''});
      return true;

    },
    // 
    uploadSuccess(res) {
      // res
      // 
      console.log("" + res)
      this.$Notice.info({title: ''});
      let quill = this.$refs.myQuillEditor.quill
      // 
      if (res.key != null) {
        this.$Notice.info({title: ''});
        // 
        console.log(res)
        if (this.ss == null) {
          this.ss = 'http://qmaqpkabs.hn-bkt.clouddn.com/' + res.key;
        }
        let length = quill.getSelection().index;
        //   res.info
        quill.insertEmbed(length, 'image', 'http://qmaqpkabs.hn-bkt.clouddn.com/' + res.key)
        // 
        quill.setSelection(length + 1)
      } else {
        console.log("" + res)
        this.$message.error('')
      }
      // loading
      this.quillUpdateImg = false
    },
    // 
    uploadError(res, file) {
      // loading
      this.quillUpdateImg = false
      this.$message.error('')
    },
    onEditorChange() {
//
      console.log("aaa" + this.detailContent)
    },
    onEditorReady() {
//
    },
    changePage(index) {
      this.init(index)
    },
    save() {
      //fa
      if (typeof (sessionStorage.getItem('_id')) !== 'undefined') { //
        let send_data = {
          time: this.article.time,
          context: this.detailContent,
          description: this.article.description,
          title: this.article.title,
          img: this.ss ? this.ss : '',
          username: sessionStorage.getItem('username'),
          userAvatar: sessionStorage.getItem('userAvatar')
        }
        // console.log(send_data)
        this.$http.post('http://kr.xj173.cn:5050/users/postArticle', send_data).then((data) => {
          console.log(data.body)
          this.$Notice.success({title: ""});
          this.init(this.index);
        })
        this.detailContent = '';
        this.article.description = '';
        this.article.title = '';
        this.ss=''
      } else {
        let send_data = {
          time: this.article.time,
          context: this.detailContent,
          description: this.article.description,
          title: this.article.title,
        }
        // console.log(send_data)
        this.$http.post('http://kr.xj173.cn:5050/users/postArticle', send_data).then((data) => {
          console.log(data.body)
          this.$Notice.success({title: ""});
          this.init(this.index);
        })
        this.detailContent = '';
        this.article.description = '';
        this.article.title = '';
        this.ss=''
      }


    },
    init: function (index) {
      this.$http.post('http://kr.xj173.cn:5050/users/showArticle', {start: index, limit: this.pageSize}).then((data) => {
        if (data.body.status == 0) {
          this.items = data.body.data;//

          this.$http.post('http://kr.xj173.cn:5050/users/articleNum', {article_id: this.article_id}).then((data) => {
            if (data.body.status == 0) {
              this.totalNum = data.body.data;
            } else {
              alert('')
            }
          });
          console.log(this.totalNum)
          console.log(this.items)

          this.$http.post('http://kr.xj173.cn:5050/users/showUser', {user_id: sessionStorage.getItem('_id')}).then((getUserInfo) => {
            if (getUserInfo.body.status === 0) {
              this.info = getUserInfo.body.data[0]
              console.log(this.info.userAdmin)
              if (this.info.userAdmin) {
                this.isAdmin = true
              }

            } else {
              console.log(getUserInfo.body);
              // alert(userData.body.message)
              // this.$Notice.info({title: getUserInfo.body.message});
            }
          })
        } else {
          this.$Notice.error({title: ""});
        }

      })

    },
    delArticle: function (article_id) {
      console.log(article_id)
      this.$http.post('http://kr.xj173.cn:5050/admin/delArticle', {
        article_id: article_id, username: sessionStorage.getItem('username'),
        token: sessionStorage.getItem('token'), user_id: sessionStorage.getItem('_id')
      }).then((data) => {
        this.$Notice.info({title: data.body.message});
        this.init(this.index);
      })
    },
    ToText:function(HTML) {
      let input = HTML;
      if (input == null) {
        input = 'kong'
      }
      input=input.replace(/<(style|script|iframe)[^>]*?>[\s\S]+?<\/\1\s*>/gi, '').replace(/<[^>]+?>/g, '').replace(/\s+/g, ' ').replace(/ /g, ' ').replace(/>/g, ' ');
      if(input.length>200){
        input=input.substring(0, 200) + '...'
      }
      return input
    },
    support: function (id, index) {
      if (!sessionStorage.getItem(id)) {
        console.log(id)
        this.$http.post('http://kr.xj173.cn:5050/users/supportA', {article_id: id}).then((data1) => {
          let data_temp = data1.body
          // let that = this
          console.log(data_temp)
          if (data_temp.status === 0) {

            this.items[index].num = data_temp.data;
            sessionStorage.setItem(id, '1')
            this.$Notice.info({title: ""})

          } else {
            this.$Notice.error({title: data_temp.message})
          }
        })
      } else {
        this.$Notice.info({title: ""})
      }

    },
    getImg($event) {
      console.log($event.target)
      if (e.target.tagName.toUpperCase() === 'IMG') {
        console.log(e.target.src)
      }
    }

  },
  created: function (index) {
    //    
    this.init(index);

  },
}
</script>

<style scoped>
.tt {
  width: 1330px;
  padding: 5px;
  border-radius: 5px;
}

.ttb {
  width: 1330px;
  padding: 5px;
  border-radius: 5px;
  border: gray 1px solid;
}
</style>



