<template>
  <div style="height: 100%">
    <Card style="margin: 100px 40px 20px 40px;border-radius: 20px;padding: 30px">
      <div>
        <movie-index-header></movie-index-header>
      </div>
      <div style="text-align: left">
        <avatar :src="detail.userAvatar" size="large" ></avatar>
        <span style="font-size: 23px;margin-left:20px ">{{ detail.username}}
        </span>
        <span style="text-align:right;margin-left: 1100px">
          <li style="list-style: none;cursor: pointer" v-on:click="support(detail._id)">
                <Icon type="ios-thumbs-up-outline"/>
                {{detail.num}}
              </li>
        </span>
      </div>
      <Divider orientation="right">{{detail.time }}</Divider>
      <div style="text-align: left;margin: 10px;padding:10px;">
        <h1 style="text-align: center">{{detail.title}}</h1>
        <div style="text-align: center;font-style: italic;text-decoration: underline">
        {{detail.description }}
        </div>
        <div style="margin: 10px" v-html="detail.context"></div>
      </div>
    </Card>
  </div>
</template>

<script>
import MovieIndexHeader from "../components/MovieIndexHeader";
export default {
name: "articleDetail",
  components: {MovieIndexHeader},
  data(){
  return{
    detail:[],
  }
  },
  methods:{
    support: function (id) {
      if (!sessionStorage.getItem(id)) {
        console.log(id)
        this.$http.post('http://kr.xj173.cn:5050/users/supportA', {article_id: id}).then((data1) => {
          let data_temp = data1.body
          // let that = this
          console.log(data_temp)
          if (data_temp.status === 0) {
            this.detail.num = data_temp.data;
            sessionStorage.setItem(id, '1')
            this.$Notice.info({title: ""})
          } else {
            this.$Notice.error({title: data_temp.message})
          }
        })
      } else {
        this.$Notice.info({title: ""})
      }
    }
  },
  created() {
  let article_id=this.$route.query.id;
    this.$http.post('http://kr.xj173.cn:5050/users/showArticleDetail', {article_id: article_id}).then((data) => {
      this.detail = data.body.data[0];
      console.log(this.$route.query.id);
      console.log(this.detail);
    })
  }
}
</script>

<style scoped>

</style>



